.vbox-overlay *,
.vbox-overlay :after,
.vbox-overlay :before {
  -webkit-backface-visibility: hidden;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box
}

.vbox-overlay * {
  -webkit-backface-visibility: visible;
  backface-visibility: visible
}

.vbox-overlay {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 999999
}

.vbox-title {
  width: 100%;
  height: 40px;
  float: left;
  text-align: center;
  line-height: 28px;
  font-size: 12px;
  padding: 6px 50px;
  overflow: hidden;
  position: fixed;
  display: none;
  left: 0;
  z-index: 89
}

.vbox-close {
  cursor: pointer;
  position: fixed;
  top: -1px;
  right: 0;
  width: 50px;
  height: 40px;
  padding: 6px;
  display: block;
  background-position: 10px center;
  overflow: hidden;
  font-size: 24px;
  line-height: 1;
  text-align: center;
  z-index: 99
}

.vbox-left {
  cursor: pointer;
  position: fixed;
  left: 0;
  height: 40px;
  overflow: hidden;
  line-height: 28px;
  font-size: 12px;
  z-index: 99;
  display: flex;
  align-items: center
}

.vbox-num {
  display: inline-block;
  margin: 6px 0 6px 15px
}

.vbox-share {
  line-height: 28px;
  font-size: 12px;
  overflow: hidden;
  position: fixed;
  left: 0;
  z-index: 98;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center
}

.vbox-share svg {
  max-height: 28px;
  width: 28px;
  z-index: 10;
  margin-left: 12px;
  margin-top: 6px;
  margin-bottom: 6px;
  vertical-align: middle
}

.vbox-next,
.vbox-prev {
  position: fixed;
  top: 50%;
  margin-top: -15px;
  overflow: hidden;
  cursor: pointer;
  display: block;
  width: 45px;
  height: 45px;
  z-index: 99
}

.vbox-next span,
.vbox-prev span {
  position: relative;
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top-color: #b6b6b6;
  border-right-color: #b6b6b6;
  text-indent: -100px;
  position: absolute;
  top: 8px;
  display: block
}

.vbox-prev {
  left: 15px
}

.vbox-next {
  right: 15px
}

.vbox-prev span {
  left: 10px;
  -ms-transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg)
}

.vbox-next span {
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  right: 10px
}

.vbox-inline {
  width: 420px;
  height: 315px;
  height: 70vh;
  padding: 10px;
  background: #fff;
  margin: 0 auto;
  overflow: auto;
  text-align: left
}

.venoframe {
  max-width: 100%;
  width: 100%;
  border: none;
  width: 100%;
  height: 260px;
  height: 70vh
}

.venoframe.vbvid {
  height: 260px
}

@media (min-width:768px) {

  .vbox-inline,
  .venoframe {
    width: 90%;
    height: 360px;
    height: 70vh
  }

  .venoframe.vbvid {
    width: 640px;
    height: 360px
  }
}

@media (min-width:992px) {

  .vbox-inline,
  .venoframe {
    max-width: 1200px;
    width: 80%;
    height: 540px;
    height: 70vh
  }

  .venoframe.vbvid {
    width: 960px;
    height: 540px
  }
}

.vbox-open {
